//index.js  React   , Redux     
//Redux Provider     ,       

import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './components/App'; // components App.js 
import reportWebVitals from './reportWebVitals';
import 'bootstrap/dist/css/bootstrap.min.css'; //  import
import { Provider } from 'react-redux';
import store from './_reducers/store';
import { getAuthToken, getUserRole, getHasPortfolio } from './hoc/request';
import { loginSuccess } from './_actions/actions';

const storedAuthToken = getAuthToken(); //   
const userRole = getUserRole(); //    
const hasPortfolio = getHasPortfolio(); //       

const localStorageCleared = localStorage.getItem('localStorageCleared'); //    

if (!localStorageCleared) { //    (   ),f5 index.js ,    localStoragecleared true storedToken   store        
    
    // Clear localStorage
    localStorage.clear();  // 
    // Mark localStorage as cleared to avoid repeating clearing
    localStorage.setItem('localStorageCleared', 'true'); //   
}

if (storedAuthToken) { //      -> f5 index.js  ,      

    //      
    store.dispatch(loginSuccess(storedAuthToken, userRole, hasPortfolio));
}
    
ReactDOM.createRoot(document.getElementById('root')).render(
    <Provider store={store}> 
        <App />
    </Provider>
);


// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
