// store.js
// Redux  ,      . 
// Redux            
//        ?    
// action.js    -> action.js       switch  

import { createStore, applyMiddleware } from 'redux';
import thunk from 'redux-thunk';

// initialState:     .  isAuthenticated authToken  
const initialState = {
    isAuthenticated: false,
    authToken: null,
    userRole: null,
    userPortfolio: null
};

/**
 * Redux   ,           .
 *  store.js     ,   actions.js import     .
 */

//authReducer:      
    // switch        .
    // LOGIN_SUCCESS  isAuthenticated true, authToken   .
    // LOGOUT  isAuthenticated false, authToken null .
    //     .
    
const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'LOGIN_SUCCESS':
            return {
                ...state,
                isAuthenticated: true, //   ,  
                authToken: action.payload.token, //   ,   
                userRole: action.payload.role, //   ,  role  
                userPortfolio: action.payload.isCreated //   
            };
        case 'LOGOUT':
            return {
                ...state,
                isAuthenticated: false, //   ,   
                authToken: null, //   , 
                userRole: null, //  , role 
                userPortfolio: null //  , portfolio 
            };
        //    .
        //   userPortfolio: true 
        case 'UPLOAD_PORTFOLIO_SUCCESS':
            return {
                ...state,               //    ,
                userPortfolio: true     // userPortfolio  true .
            };
        


        //    .
        //   userPortfolio: false 
        default:
            return state;
    }
};
    


// 3.  (store):

    // createStore   Redux  .
    // authReducer  , applyMiddleware    thunk .
    //        .
    //    ' '   ,           .
const store = createStore(authReducer, applyMiddleware(thunk));

export default store;
