import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { Row, Col, Input, Button, Radio, message } from 'antd';
import { request, setHasPortfolio } from '../../../../hoc/request';
import { uploadPortfolioSuccess } from '../../../../_actions/actions';

function PortfolioPage() {
    const navigate = useNavigate();
    const dispatch = useDispatch();
    
    //     state 
    const [shortIntroduce, setShortIntroduce] = useState('login'); //   
    const [introduce, setIntoduce] = useState(''); //   
    const [fileUrl, setFileUrl] = useState('');   //  
    const [preferences, setPreferences] = useState({    //   
        web: 0,
        app: 0,
        game: 0,
        ai: 0
    });

    //       
    const onChangeHandler = (event) => {
        const name = event.target.name;
        const value = event.target.value;
        
        //  (Input) name   ,  state  
        if (name === 'shortIntroduce') setShortIntroduce(value);
        else if (name === 'introduce') setIntoduce(value);
        else if (name === 'fileUrl') setFileUrl(value);
    };

    // field  web, app, game, ai .
    // value  0, 1, 2, 3, 4 .
    const handlePreferenceChange = (field, value) => {
        // 0   .      .
        if (value === 0 || !Object.values(preferences).includes(value)) {
            const newPreferences = { ...preferences, [field]: value };
            setPreferences(newPreferences);
        }
        else {
            //      .
            message.warning('     .');
        }
    };


    // Web, App, Game, Ai  0, 1, 2, 3, 4      .
    const renderRadioGroup = (field) => (
        <Radio.Group
            value={preferences[field]}
            onChange={(e) => handlePreferenceChange(field, e.target.value)}
        >
            <Radio value={0}>0</Radio>
            <Radio value={1}>1</Radio>
            <Radio value={2}>2</Radio>
            <Radio value={3}>3</Radio>
            <Radio value={4}>4</Radio>
        </Radio.Group>
    );

    //       
    const onSubmitPortfolio = (e) => {
        e.preventDefault();
        // web, app, game, ai      , preferences  .
        submitPortfolio(e, preferences.web, preferences.app, preferences.game, preferences.ai, shortIntroduce, introduce, fileUrl);
        navigate('/');
    };


    //   
    const submitPortfolio = (event, web, app, game, ai, shortIntroduce, introduce, fileUrl) => {
        event.preventDefault();

        request('POST', '/uploadPortfolio', {
            web: web,
            app: app,
            game: game,
            ai: ai,
            shortIntroduce: shortIntroduce,
            introduce: introduce,
            fileUrl: fileUrl
        })
            .then((response) => {
                dispatch(uploadPortfolioSuccess(response.data.isCreated)); // Dispatch login success action with role
                setHasPortfolio(response.data.isCreated);
                alert('  .');
            })
            .catch((error) => {
                alert('  .');
            });
    };


    return (
        <Row justify="center">
            <Col span={12}>
                <form onSubmit={onSubmitPortfolio}>
                    {/** mb-4 : "margin Bottom 4"        */}
                    <p>   .   ,     . 4   , 0   .   (0)    ,      . </p>
                    <div className="form-outline mb-4">
                        <table>
                            <tbody>
                                <tr>
                                    <td>Web</td>
                                    <td>{renderRadioGroup('web')}</td>
                                </tr>
                                <tr>
                                    <td>App</td>
                                    <td>{renderRadioGroup('app')}</td>
                                </tr>
                                <tr>
                                    <td>Game</td>
                                    <td>{renderRadioGroup('game')}</td>
                                </tr>
                                <tr>
                                    <td>AI</td>
                                    <td>{renderRadioGroup('ai')}</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                    <div className="form-outline mb-4">
                        <Input
                            type="text"
                            name="shortIntroduce"
                            placeholder="   ."
                            onChange={onChangeHandler}
                        />
                    </div>
                    <div className="form-outline mb-4">
                        <Input
                            type="text"
                            name="introduce"
                            placeholder="   ."
                            onChange={onChangeHandler}
                        />
                    </div>
                    <div className="form-outline mb-4">
                        <Input
                            type="text"
                            name="fileUrl"
                            placeholder="  ."
                            onChange={onChangeHandler}
                        />
                    </div>
                    <Button type="primary" block htmlType="submit">Create Portfolio</Button>
                </form>
            </Col>
        </Row>
    );
}

export default PortfolioPage;
