//      .
// type      'LOGIN_SUCCESS' .
// payload      .
export const loginSuccess = (token, role, isCreated) => {
    return {
        type: 'LOGIN_SUCCESS',
        payload: { token, role, isCreated }
    };
};



//     .
// type      'LOGOUT' .
export const logout = () => {
    //window.localStorage.removeItem('auth_token');
    return {
        type: 'LOGOUT'
    };
};



//      
export const uploadPortfolioSuccess = (isCreated) => {
    return {
        type: 'UPLOAD_PORTFOLIO_SUCCESS',   // type 
        payload: { isCreated }              //   isCreated payload 
    };
}


