//  

// app.js node module   initialize   Object  Router       JavaScript

import React/*, { useEffect }*/ from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { Layout } from 'antd';
import Auth from '../hoc/auth';
import MyHeader from './views/MyHeader/MyHeader';
import AdminPage from './views/AdminPage/AdminPage';
import MyPage from './views/MyPage/MyPage';
import ScrapPage from './views/ScrapPage/ScrapPage';
import PortfolioPage from './views/PortfolioPage/PortfolioPage';
import UploadPortfolioPage from './views/PortfolioPage/UploadPortfolioPage/UploadPortfolioPage'
import UpdatePortfolioPage from './views/PortfolioPage/UpdatePortfolioPage/UpdatePortfolioPage';
import DeletePortfolioPage from './views/PortfolioPage/DeletePortfolioPage/DeletePortfolioPage';
import GroupPage from './views/GroupPage/GroupPage';
import LandingPage from './views/LandingPage/LandingPage';
import LoginPage from './views/LoginPage/LoginPage';
import RecommendationPage from './views/RecommendationPage/RecommendationPage';
import ProjectPage from './views/ProjectPage/ProjectPage';
import StudyPage from './views/StudyPage/StudyPage';
import UploadPostPage from './views/UploadPostPage/UploadPostPage';
import Footer from './views/Footer/Footer';
import './App.css';
/*import logo from '../logo.svg';
import logo1 from '../pickme.PNG';*/
import logo2 from '../pickme2.PNG'


const { Content } = Layout;

function App() {
    //    : 
    // 1. useNavigate    .
    // 2. F5    ,  .
    // useEffect(() => {
    //     localStorage.clear(); //  (  )   
    //     //   key   localStorage.removeItem('key') .
    //   }, []); //          .

    return (
        <Router>
            <Layout>
                {/**     */}
                <MyHeader logoSrc={logo2} />
                <Content style={{ padding: '20px' }}>
                    <div style={{ paddingLeft : '15%', paddingRight : '15%' }}>
                    <Routes>
                        {
                            /**
                             *  
                             * 
                             * Auth(, null) ->     
                             * => , ,    .
                             * 
                             * Auth(, true) ->  (ADMIN, USER)    
                             * =>    EndPointer  . ,   Endpointer  
                             * 
                             * Auth(, false) ->  (ADMIN, USER)    
                             * =>    EndPointer  . ,   Endpointer  
                             * 
                             * Auth(, null, true) -> (ADMIN)    
                             * =>       EndPointer  .   Endpointer  
                             * 
                             * Auth(, true, null, true) -> (USER)         
                             * =>     Endpointer  
                             * 
                             * Auth(, true, null, false) -> (USER)        
                             * =>      Endpointer  
                             * 
                             * 
                             * 
                             * 
                             * Auth(, null)     .
                             *  isAuthenticated && userRole === 'ADMIN'  ,  (, , )    .
                             * 
                             * Auth(, true)     .
                             *  isAuthenticated && userRole === 'ADMIN'  ,  (, )    .
                             * 
                             * Auth(, false)     .
                             *    ,    .
                             * 
                             * Auth(, null, true)    .
                             *     .
                             * 
                             * Auth(, true, null, true)           .
                             *        ,    .
                             * 
                             * Auth(, true, null, false)          .
                             *       ,    .
                             * 
                             */
                        }
                        <Route
                            path="/"
                            // element={<LandingPage/>}
                            element={Auth(LandingPage, null)}
                        />
                        <Route
                            path="/adminPage"
                            // element={<AdminPage/>}
                            element={Auth(AdminPage, null, true)}
                        />
                        <Route
                            path="/myPage"
                            // element={<MyPage/>}
                            element={Auth(MyPage, true)}
                        />
                        <Route
                            path="/scrap"
                            // element={<ScrapPage/>}
                            element={Auth(ScrapPage, true)}
                        />
                        <Route
                            path="/portfolio"
                            // element={<PortfolioPage/>}
                            element={Auth(PortfolioPage, true)}
                        />
                        <Route
                            // path   /  !! path="portfolio/upload"  path="/portfolio/upload"!!
                            path="/portfolio/upload"
                            // User , Portfolio       
                            element={Auth(UploadPortfolioPage, true, null, true)}
                        />
                        <Route
                            path="/portfolio/update"
                            // User , Portfolio      
                            element={Auth(UpdatePortfolioPage, true, null, false)}
                        />
                        <Route
                            path="/portfolio/delete"
                            // User , Portfolio      
                            element={Auth(DeletePortfolioPage, true, null, false)}
                        />
                        <Route
                            path="/group"
                            // element={<GroupPage/>}
                            element={Auth(GroupPage, true)}
                        />
                        <Route
                            path="/login"
                            // element={<LoginPage/>}
                            element={Auth(LoginPage, false)}
                        />
                        <Route
                            path="/Recommendation"
                            // element={<RecommendationPage/>}
                            element={Auth(RecommendationPage, true)}
                        />
                        <Route
                            path="/Project"
                            // element={<ProjectPage/>}
                            element={Auth(ProjectPage, true)}
                        />
                        <Route
                            path="/Study"
                            // element={<StudyPage/>}
                            element={Auth(StudyPage, true)}
                        />
                        <Route
                            path="/uploadPost"
                            // element={<UploadPostPage/>}
                            element={Auth(UploadPostPage, true)}
                        />
                        
                    </Routes>
                    </div>
                </Content>
                <Footer style={{ paddingLeft : '15%', paddingRight : '15%' }} logoSrc={logo2}/>
            </Layout>
        </Router>
    );
}

// es6    export ,    CommonJS module.exports .
// https://www.daleseo.com/js-module-import/
export default App;

// export default :
//   export     export default  (,   ) 
//    export default           

// export : export  (,   )  import {  1, 2,3, } from .js   