import React from 'react';
import { Row, Col } from 'antd';
import { useSelector } from 'react-redux';

import WelcomeContent from './Sections/WelcomeContent';
import Auth from '../Auth/Auth';
import RecommendationCard from './Sections/RecommendationCard';
import ProjectCard from './Sections/ProjectCard';
import StudyCard from './Sections/StudyCard';
import Search from '../../utils/Search';

function LandingPage() {
    // Use useSelector to access isAuthenticated state from Redux store
    //( : index.js    provider  store ,
    //store.js       ,
    //useSelector redux import     state     )
    const isAuthenticated = useSelector(state => state.isAuthenticated);
    const userRole = useSelector(state => state.userRole);

    return (
        <div>
            {/* Conditional rendering based on authentication status */}
            {!isAuthenticated && ( //      
                <Row gutter={[16, 16]}>
                    <Col span={24}>
                        <WelcomeContent />
                    </Col>
                    <Col span={8}>
                        <RecommendationCard />
                    </Col>
                    <Col span={8}>
                        <ProjectCard />
                    </Col>
                    <Col span={8}>
                        <StudyCard />
                    </Col>
                </Row>
            )}
            {isAuthenticated && userRole === 'ADMIN' && ( //,     
                <Row gutter={[16, 16]}>
                    <Col span={24}>
                        <h2> THIS IS AN ADMIN PAGE </h2>
                        {/* This section is only visible to logged-in members */}
                        <Auth />
                    </Col>
                    <Search/>
                    <Col span={8}>
                        <RecommendationCard />
                    </Col>
                    <Col span={8}>
                        <ProjectCard />
                    </Col>
                    <Col span={8}>
                        <StudyCard />
                    </Col>
                </Row>
            )}
            {isAuthenticated && userRole === 'USER' && ( //     
                <Row gutter={[16, 16]}>
                    <Col span={24}>
                        <h2> THIS IS AN USER PAGE </h2>
                        {/* This section is only visible to logged-in members */}
                        <Auth />
                    </Col>
                    <Search/>
                    <Col span={8}>
                        <RecommendationCard />
                    </Col>
                    <Col span={8}>
                        <ProjectCard />
                    </Col>
                    <Col span={8}>
                        <StudyCard />
                    </Col>
                </Row>
            )}
        </div>
    );
}

export default LandingPage;
